<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav
      :title="`${GETTER_MECHANTNAME !== '' ? GETTER_MECHANTNAME + ' - ' : ''}預約審查`"
      :routeTarget="{ name: 'Order' }"
    ></C-Back-Nav>
    <section v-if="resultItem" class="relative z-0 mt-[52px] p-3">
      <div
        class="m-auto mt-4 max-w-[475px] rounded-lg p-2 text-left shadow-[0_4px_10px_3px_rgba(0,0,0,0.1)]"
      >
        <p class="mb-2 text-center text-lg font-semibold">
          {{ $route.query.isReview ? "預約內容" : "預約明細" }}
        </p>
        <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">預約時間:</p>
          <p class="w-1/3" v-if="isEdit">
           <vc-date-picker v-model="resultItem.date" mode="dateTime" is24hr :min-date="$dayjs().format('YYYY-MM-DD')">
          <template v-slot="{ inputValue, inputEvents }">
            <input
              class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
              placeholder="請選擇日期"
              :value="inputValue"
              v-on="inputEvents"
            />
          </template>
        </vc-date-picker>
        </p>
          <p class="w-1/3" v-if="!isEdit">{{ resultItem.date | dateFmtROCMMDD }}</p>
          <p class="w-1/3" v-if="!isEdit">{{ resultItem.date | dateFmtHm }}</p>
        </div>

        <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">姓名:</p>
          <C-Input v-if="isEdit" v-model="resultItem.customer.name" :placeholder="'輸入客戶姓名'" class="!m-0"></C-Input>
          <p class="w-2/3" v-else>{{resultItem.customer.name}}</p>
        </div>

        <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">電話:</p>
          <C-Input v-if="isEdit" v-model="resultItem.customer.cellphone" :placeholder="'輸入客戶姓名'" class="!m-0"></C-Input>
          <p class="w-2/3" v-else>{{resultItem.customer.cellphone}}</p>
        </div>

        <!-- <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">服務類別:</p>
          <p class="w-2/3">{{resultItem.type}}</p>
        </div> -->

        <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">設計師:</p>
          <p class="w-2/3">
            {{ resultItem.designer ? resultItem.designer.name : resultItem.designer }}
          </p>
        </div>
        <div class="mb-2 flex items-center border-b border-solid border-primary/5 pb-1">
          <p class="w-1/3 font-bold">{{isEdit?"指定預約":"指定/不指定"}}</p>
         <C-Checkbox-Full v-model="resultItem.isDesignate" class="!ml-0 !w-fit" v-if="isEdit" label="指定/不指定"></C-Checkbox-Full>
          <p class="w-2/3" v-else>{{ resultItem.isDesignate?"指定":"不指定" }}</p>
        </div>
        <div class="mb-2 flex border-b border-solid border-primary/5 pb-1">
          <label class="w-1/3 font-bold">我的備註:</label>
          <div class="relative w-2/3">
            <textarea v-if="isEdit" name="remark" v-model="resultItem.remark"  rows="5"
              class="w-full rounded-md border border-primary/50"></textarea>
            <p v-else >{{ resultItem.remark }}</p>
            <font-awesome-icon v-if="resultItem.remark !== '' && isEdit" @click="resultItem.remark = ''"
              icon="fa-regular fa-xmark" class="absolute right-2 bottom-2 text-rose-600"/>
          </div>
        </div>
        <div class="mb-2 flex border-b border-solid border-primary/5 pb-1">
          <label class="w-1/3 font-bold">顧客備註:</label>
          <div class="relative w-2/3">
            <p name="remark" class="w-full">{{ resultItem.remarkForCustomer }}</p>
          </div>
        </div>
        <div class="mb-2 flex border-b border-solid border-primary/5 pb-1">
          <label class="w-1/3 font-bold">留言給顧客:</label>
          <div class="relative w-2/3">
            <textarea v-if="isEdit" name="messageForCustomer" v-model="resultItem.messageForCustomer"  rows="5" class="w-full rounded-md border border-primary/50"></textarea>
            <p v-else >{{resultItem.messageForCustomer}}</p>
            <font-awesome-icon v-if="resultItem.messageForCustomer !== '' && isEdit" @click="resultItem.messageForCustomer = ''" icon="fa-regular fa-xmark" class=" absolute right-2 bottom-2 text-rose-600"/>
          </div>
        </div>
      </div>
      <div class="mt-4" v-if="resultItem.status === '待審查'">
        <p class="text-center text-lg font-semibold">選擇審查狀態</p>
        <div class="m-auto flex max-w-[475px] justify-around p-2">
          <div
            class="w-1/3 cursor-pointer rounded bg-sucessText px-[14px] py-2 text-sm text-white"
            @click="toggleDialog('accept')"
            @keypress="toggleDialog('accept')"
          >
            接單
          </div>
          <div
            class="w-1/3 cursor-pointer rounded bg-rejectText px-[14px] py-2 text-sm text-white"
            @click="toggleDialog('reject')"
            @keypress="toggleDialog('reject')"
          >
            拒絕
          </div>
        </div>
      </div>
      <!--
        當訂單為已完成時，但表訂單不得再更改是否接受預約
       -->
      <div class="mt-4" v-if="resultItem.status !== '待審查' && resultItem.status !== '已完成'">
        <p class="text-center text-lg font-semibold">選擇審查狀態</p>
        <div class="m-auto flex max-w-[475px] justify-around p-2">
          <div
            class="flex w-1/3 cursor-pointer items-center justify-center gap-2 rounded bg-sucessText px-[14px] py-2 text-sm text-white"
            @click="updateVerifyBookingInput(resultItem, true)"
            @keypress="updateVerifyBookingInput(resultItem, true)"
          >
            <font-awesome-icon
              icon="fa-regular fa-circle-check"
              class="animate__animated animate__heartBeat animate__fast text-xl"
              v-if="resultItem.status === '預約成功'"
            />
            <span>接單</span>
          </div>
          <div
            class="flex w-1/3 cursor-pointer items-center justify-center gap-2 rounded bg-rejectText px-[14px] py-2 text-sm text-white"
            @click="updateVerifyBookingInput(resultItem, false)"
            @keypress="updateVerifyBookingInput(resultItem, false)"
          >
            <font-awesome-icon
              icon="fa-regular fa-circle-check"
              class="animate__animated animate__heartBeat animate__fast text-xl"
              v-if="resultItem.status === '設計師已取消'"
            />
            <span>拒絕</span>
          </div>
        </div>
      </div>

      <div class="m-auto flex max-w-[475px] justify-around p-2" v-if="resultItem.status !== '0'">
        <div
          class="w-full cursor-pointer bg-second py-2 px-4 text-white"
          @click="doSubmitEdit(), (isEdit = !isEdit)"
          @keypress="doSubmitEdit(), (isEdit = !isEdit)"
        >
          {{ isEdit ? "儲存預約" : "修改預約" }}
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'order-edit',
  description: '預約單的審查',
  metaInfo: {
    title: 'BeautyOS 設計師',
    titleTemplate: '%s | 預約',
  },
  mounted() {
    this.init();
    // console.log(path: this.$route.path, query: { id: this.resultItem.id, isReview: this.$route.query.isReview });
  },
  data() {
    return {
      isEdit: false,
      resultItem: null,
    };
  },
  methods: {
    async init() {
      await this.getOrder(this.$route.query.id);
    },
    doRoute() {
      this.$router.push({ name: 'Order' });
    },
    async getOrder(orderId) {
      const res = await this.$api.order({ orderId });
      const { data, errors } = res.data;
      if (errors) return {};
      this.resultItem = data.order;
      return res;
    },
    async doSubmitEdit() {
      if (!this.isEdit) return;
      console.log(this.resultItem.isDesignate);
      const resp = await this.$api.updateBooking({
        id: this.$route.query.id,
        remark: this.resultItem.remark,
        isDesignate: this.resultItem.isDesignate,
        messageForCustomer: this.resultItem.messageForCustomer,
        date: new Date(this.resultItem.date).getTime(),
        customerCellphone: this.resultItem.customer.cellphone,
        customerName: this.resultItem.customer.name,
      });
      const { data, errors } = resp.data;
      if (errors) return;
      this.resultItem = data.updateBooking;
      this.$swal
        .fire({
          title: ' ',
          html: `<div class="flex items-center justify-center">
          <img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>修改成功</span>
        </div>
        <br/>`,
          confirmButtonText: '回到列表',
          showConfirmButton: true,
          showCloseButton: true,
          customClass: {
            confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
          },
        })
        .then((res) => {
          if (res.isConfirmed) {
            this.doRoute();
          }
        });
    },
    toggleDialog(actionType) {
      this.$swal
        .fire({
          input: 'text',
          title: ' ',
          html: `<div class="flex items-center justify-center">
          ${
  actionType === 'accept'
    ? '<img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>確認接單</span>'
    : '<img src="/designer/assets/images/error.svg" alt="" class="inline-block mr-1" /><span>已拒絕</span>'
}
        </div>`,
          inputPlaceholder: '請輸入備註說明',
          confirmButtonText: '送出',
          showConfirmButton: true,
          showCloseButton: true,
          showClass: {
            popup: 'animate__animated animate__fadeInDown',
          },
          hideClass: {
            popup: 'animate__animated animate__fadeOutUp animate__faster',
          },
          customClass: {
            confirmButton: '!w-[144px] !rounded !bg-second !px-[14px] !py-2 !text-sm !text-white',
          },
        })
        .then((res) => {
          if (res.isConfirmed) {
            this.$swal
              .fire({
                showCloseButton: true,
                html: `<div class="flex items-center justify-center my-10">
              ${
  actionType === 'accept'
    ? '<img src="/designer/assets/images/success.svg" alt="" class="inline-block mr-1" /><span>接單成功</span>'
    : '<img src="/designer/assets/images/error.svg" alt="" class="inline-block mr-1" /><span>拒絕成功</span>'
}
            </div>`,
                showConfirmButton: false,
                timer: 3000,
                timerProgressBar: true,
              })
              .then(() => {
                this.doRoute();
              });
          }
        });
    },
    async updateVerifyBookingInput(resultItem, isApproval) {
      if (resultItem.status === '預約成功' && isApproval) return;
      if (resultItem.status === '設計師已取消' && !isApproval) return;
      const res = await this.$api.VerifyBookingInput({ orderId: resultItem.id, isApproval });
      const { errors } = res.data;
      if (errors) return;
      await this.getOrder(this.$route.query.id);
    },
  },
  computed: {
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
  },
};
</script>

<style lang="scss" scoped></style>
